/* Custom 3D Card Flip Styles */
.perspective-1000 {
  perspective: 1000px;
}

.transform-style-preserve-3d {
  transform-style: preserve-3d;
}

.backface-hidden {
  backface-visibility: hidden;
}

.rotate-y-180 {
  transform: rotateY(180deg);
}

/* F1 Racing Animations */
@keyframes racecar {
  0% { transform: translateX(-100vw); }
  100% { transform: translateX(100vw); }
}

@keyframes racecar-formation {
  0% { transform: translateX(-50px) translateY(0); opacity: 0; }
  20% { opacity: 1; }
  100% { transform: translateX(50px) translateY(-10px); opacity: 0.8; }
}

@keyframes sparkle {
  0%, 100% { opacity: 0; transform: scale(0); }
  50% { opacity: 1; transform: scale(1); }
}

@keyframes track-lights {
  0% { background-color: #ef4444; }
  25% { background-color: #f59e0b; }
  50% { background-color: #22c55e; }
  75% { background-color: #3b82f6; }
  100% { background-color: #ef4444; }
}

@keyframes trophy-bounce {
  0%, 20%, 50%, 80%, 100% { transform: translateY(0); }
  40% { transform: translateY(-10px); }
  60% { transform: translateY(-5px); }
}

@keyframes podium-rise {
  0% { transform: translateY(100px); opacity: 0; }
  100% { transform: translateY(0); opacity: 1; }
}

@keyframes checkered-flag {
  0% { background-position: 0 0; }
  100% { background-position: 40px 40px; }
}

.racing-stripes {
  background-image: repeating-linear-gradient(
    45deg,
    #000 0px,
    #000 10px,
    #fff 10px,
    #fff 20px
  );
  animation: checkered-flag 0.5s infinite linear;
}

.circuit-pattern {
  background-image: 
    radial-gradient(circle at 20% 50%, #333 2px, transparent 2px),
    radial-gradient(circle at 80% 50%, #333 2px, transparent 2px);
  background-size: 100px 50px;
}

/* Glowing effects */
.glow-yellow {
  box-shadow: 0 0 20px rgba(255, 255, 0, 0.5);
}

.glow-red {
  box-shadow: 0 0 20px rgba(255, 0, 0, 0.5);
}

.glow-green {
  box-shadow: 0 0 20px rgba(0, 255, 0, 0.5);
}

/* Tire smoke effect */
@keyframes smoke {
  0% {
    opacity: 0.8;
    transform: scale(1) translateX(0);
  }
  100% {
    opacity: 0;
    transform: scale(3) translateX(50px);
  }
}

.smoke-effect::before {
  content: '';
  position: absolute;
  width: 10px;
  height: 10px;
  background: #666;
  border-radius: 50%;
  animation: smoke 2s infinite;
}

/* Victory celebration animations */
@keyframes confetti {
  0% {
    transform: translateY(-100vh) rotate(0deg);
    opacity: 1;
  }
  100% {
    transform: translateY(100vh) rotate(720deg);
    opacity: 0;
  }
}

.confetti {
  position: fixed;
  width: 10px;
  height: 10px;
  background: #ffd700;
  animation: confetti 3s infinite ease-in;
}

.confetti:nth-child(2n) { background: #ff6b6b; }
.confetti:nth-child(3n) { background: #4ecdc4; }
.confetti:nth-child(4n) { background: #45b7d1; }
.confetti:nth-child(5n) { background: #96ceb4; }
